import React, { memo } from 'react';
import { useStoreon } from 'storeon/react';
import CartPageLayoutContainer from './CartPageLayout/CartPageLayoutContainer';

const CartPage = ({
    context
}) => {

    const { cartStore, recommendedProductsStore ,currency, listOrdersStore, balanceStore, numberCurrentOrderForAddProduct} = useStoreon('numberCurrentOrderForAddProduct','currency','cartStore', 'recommendedProductsStore', 'listOrdersStore', 'balanceStore');
    const {
        listCurrentOrder,
        breadcrumbs,
        page_info,
        profile,
    } = context;

    const textConditionPayPart_1 = page_info?.components[0]?.children[0]?.content;
    const textConditionPayPart_2 = page_info?.components[0]?.children[1]?.content;

    const {
        valueButtonNextToOrder,
        agreeWitheRegulations,
        enableAllSelect,
        total_discount,
        isLoadingCart,
        is_performed,
        cartitem_set,
        total_price,
        in_stock,
        selected,
        in_cart,
        can_add,
    } = cartStore;
    const {
        role,
        cart,
    } = profile;
    const opt_minimum_price = balanceStore.opt_minimum_price;
    return (
        <CartPageLayoutContainer
            numberCurrentOrderForAddProduct={numberCurrentOrderForAddProduct}
            textConditionPayPart_1={textConditionPayPart_1}
            textConditionPayPart_2={textConditionPayPart_2}
            valueButtonNextToOrder={valueButtonNextToOrder}
            agreeWitheRegulations={agreeWitheRegulations}
            recommendedProducts={recommendedProductsStore}
            opt_minimum_price={opt_minimum_price}
            listCurrentOrder={listOrdersStore}
            enableAllSelect={enableAllSelect}
            total_discount={total_discount}
            profileInCart={cart}
            is_performed={is_performed}
            cartitem_set={cartitem_set}
            breadcrumbs={breadcrumbs}
            total_price={total_price}
            isLoading={isLoadingCart}
            selected={selected}
            currency={currency}
            in_stock={in_stock}
            can_add={can_add}
            in_cart={in_cart}
            role={role}
        />
    )
}

export default CartPage;