import React from 'react';
import Title from '../../../Views/Title';
import AsyncComponent from '../../../helpers/asyncComponent'
import LinkToCatalog from './DatasPage/LinkToCatalog';
import Breadcrumbs from '../../../Views/Breadcrumbs';
import TextUnderTitle from '../../../Views/TextUnderTitle';

import Container from '../../../Views/GridContainerBlock/Container';
import Row from '../../../Views/GridContainerBlock/Row/Row';
import Coll from '../../../Views/GridContainerBlock/Coll/Coll';

const AsyncYouHaveAlreadyWatched = AsyncComponent(() => {
  return import('../../../Views/YouHaveAlreadyWatched/YouHaveAlreadyWatchedContainer');
});

const DefaultCartPreview = ({ recommendedProducts, breadcrumbs, currency, role }) => {
  return (

    <Container>
      <Breadcrumbs breadcrumbs={breadcrumbs} />

      <Row>
        <Coll className="cart__empty">
          <Title variant={'cart'} type={'h1'}>
            В вашей корзине пока ничего нет
          </Title>
          <TextUnderTitle type={'text-under_title'} variant={'text-content__cart-info'} >
            Корзина ждёт, чтобы её наполнили. Желаем приятного шоппинга в мире моды!
          </TextUnderTitle>
          <LinkToCatalog to={'/catalog'}>
            СМОТРЕТЬ ТОВАРЫ
          </LinkToCatalog>
        </Coll>
      </Row>
      <Row>
        <Coll>
          {
            !!recommendedProducts.count ?
              <AsyncYouHaveAlreadyWatched
                role={role}
                headerText={'Рекомендуемое'}
                youAlredyWatch={recommendedProducts}
                currency={currency}
                isLoadingMyWish={recommendedProducts.isLoadingMyWish}
              />
              : null
          }        </Coll>
      </Row>
    </Container>
  );
};

export default (DefaultCartPreview);
